
body{
  background-color: #FFFFFF;
}
.home-header {
  background-color: #282C34;
  color: #FFFFFF;
  text-align: center;
  width: 100%;
  font-size: 70px;
  min-height: 300px;
  padding-top: 50px;
  margin-top: 10%;
}
.chevron {
  text-align: center;
  font-size: 100px;
  color: #282C34;
}
.adc:hover {
  color:#00d8ff;
  cursor: pointer;
}
.myFace {
  height: 30vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30vh;
  box-shadow: 5px 10px #4f555e;
}
.aboutBio {
  text-align: justify;
  color: black;
  padding-top: 10px;
  font-family: roboto;
  font-size: large;
}
.aboutPadding {
  padding-right: 10px;
  padding-right: 10px;
}
hr {
  width: 50%;
  height: 1px; 
  color: #00d8ff; 
  background-color: #00d8ff; 
  border: none; 
}
.divider {
  background-color: white;
}

.about {
  
  background-color:white;
  width: 50%;
  padding-top: 25px;
  padding-bottom: 25px;
}

.resume {
  color: #282C34;
  padding-top: 55px;
  padding-bottom: 25px;
  font-family: roboto;
  font-size: large;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.projects {
  color: white;
  background-color: #282C34;
  font-family: roboto;
  text-align: center;
  padding-top: 25px;
  padding-bottom: 25px;
  max-height: 500px;
  min-height: 600px;

}
.buttons {
  padding-top: 15px;
}

.projectImage {
  max-width: 50%;
  max-height: 300px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 10px 10px #4f555e;
}
.projectParent {
  position: relative;
}
.projectChild {
  background-color: rgba(96, 125, 139, 0.6);
  position: sticky;
  right: 10px;
  top:50px;
  
}
.center {
  justify-content: center;
}

.experience {
  background-color: white;
  padding-top: 25px;
  padding-bottom: 25px;
  justify-content: center;
  color: black;
}

.card {
  width: 300px;
  height: 400px;
  padding: 10px;
}

.portIcon {
  color:  #4f555e;;
  padding: 11px;

}
.portIcon:hover {
  color:#00d8ff;
  cursor: pointer;
}
.main {
  color: #FFFFFF;
}
.iconAlign {
  display: block;
  align-items: right;
}
.hover:hover{
  cursor: pointer;
}
.padding {
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 12px;
}
.nav {
  width: 100%;
}
.hover {
  padding-left: 10px;
}

.drawer {
  color: red;
}

a {
  text-decoration: none;
  color: white;
}

.hamburger
{
  color: #00d8ff;
}
.menu 
{
  background-color: red;
}
.contact {
  background-color: #282C34;
  padding: 60px;
}

.contactButton {
  padding-top:30px;
  padding-bottom: 30px;
}

.border {
  border: 5px solid red;
}
.resumeButton {
  padding: 25px 10px;
}
.footer {
  padding: 10px;
}
.footerEmail {
  padding: 10px;
}
.footer:hover {
  color: #00d8ff;
}

@media only screen and (max-width: 600px) {

  .projects {
    max-height: 850px;
    min-height: 850px;
  }
  .projectSection {
    padding: 10px;
  }
  .aboutBio {
    text-align: center;
  }
  .aboutPadding {
    padding: 5px 13px 20px 13px;
  }
} 